import Link from 'next/link';
import { Plus, Edit, Trash2 } from 'lucide-react';
import Project from '@/models/Project';
import connectToDatabase from '@/lib/db';
import ProjectDeleteButton from '@/components/admin/ProjectDeleteButton'; // Need to create this for client-side interactivity or use a form action
// Actually, for simplicity, I'll make a client component for the delete button or just render Server Component logic if I use server actions.
// Since I'm using API routes, I'll need a client component for delete.

export const dynamic = 'force-dynamic';

async function getProjects() {
    await connectToDatabase();
    const projects = await Project.find({}).sort({ order: 1, createdAt: -1 });
    return projects;
}

export default async function AdminProjects() {
    const projects = await getProjects();

    return (
        <div>
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-display text-primary">Projects</h1>
                <Link href="/admin/projects/new" className="flex items-center gap-2 bg-white text-black px-4 py-2 rounded font-semibold hover:bg-gray-200 transition-colors">
                    <Plus size={18} /> New Project
                </Link>
            </div>

            <div className="bg-surface border border-border rounded-lg overflow-hidden">
                <table className="w-full text-left">
                    <thead className="bg-surfaceHighlight">
                        <tr>
                            <th className="p-4 text-sm font-medium text-secondary">Title</th>
                            <th className="p-4 text-sm font-medium text-secondary">Tags</th>
                            <th className="p-4 text-sm font-medium text-secondary">Order</th>
                            <th className="p-4 text-sm font-medium text-secondary text-right">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-border">
                        {projects.map((project: any) => (
                            <tr key={project._id} className="hover:bg-surfaceHighlight/50 transition-colors">
                                <td className="p-4 text-primary font-medium">{project.title}</td>
                                <td className="p-4 text-secondary">
                                    <div className="flex gap-1 flex-wrap">
                                        {project.tags.slice(0, 3).map((tag: string) => (
                                            <span key={tag} className="text-xs bg-white/5 px-2 py-1 rounded">{tag}</span>
                                        ))}
                                    </div>
                                </td>
                                <td className="p-4 text-secondary">{project.order}</td>
                                <td className="p-4 text-right flex justify-end gap-3 items-center">
                                    <Link href={`/admin/projects/${project._id}`} className="text-accent hover:text-white transition-colors">
                                        <Edit size={18} />
                                    </Link>
                                    <ProjectDeleteButton id={project._id.toString()} />
                                </td>
                            </tr>
                        ))}
                        {projects.length === 0 && (
                            <tr>
                                <td colSpan={4} className="p-8 text-center text-secondary">No projects found. Create one!</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
